import './style.css';

const { Content } = Layout;

const Organization = () => {
  return (
    <div className="organization">
      <MainHeader />
      <Layout>
        <Layout>
          <Sidebar />
          <Content style={{ padding: '90px 20px 0px 20px' }}>
            <Data />
          </Content>
        </Layout>
      </Layout>
    </div>
  );
};

